.catalog-item {
    margin-right: 20px;
    margin-bottom: 30px;
    padding: 10px;
    border: 1px solid #333050;
}

.catalog-item-cover {
    position: relative;
    margin-bottom: 10px;
}

.catalog-item-cover:hover .add-to-cart-button {
    visibility: visible;
    opacity: 1;
}

.add-to-cart-button {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 20px;
    right: 20px;
    z-index: 2;
    width: 34px;
    height: 34px;
    border-radius: 50%;
    transition: var(--default-transition);
    visibility: hidden;
    opacity: 0;
    background-color: #fff;
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.15);
}

.add-to-cart-button:hover .button-label {
    visibility: visible;
    opacity: 1;
}

.button-label {
    position: absolute;
    left: -10px;
    top: 0;
    padding: 10px 12px;
    border-radius: 3px;
    transform: translateX(-100%);
    background-color: var(--default-transparent);
    transition: var(--default-transition);
    visibility: hidden;
    opacity: 0;
    white-space: nowrap;
    font-size: 14px;
    color: #fff;
}

.button-label::after {
    content: "";
    position: absolute;
    top: 50%;
    margin-top: -5px;
    right: -10px;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 5px;
    border-color: transparent transparent transparent var(--default-transparent);
}

.cover-link {
    display: block;
    width: 100%;
    height: 100%;
}

.cover-link img {
    width: 100%;
    height: 100%;
}

.catalog-item-details {
    display: flex;
    flex-direction: column;
}

.item-category {
    opacity: 0.6;
}

.item-title-link h4 {
    font-size: 18px;
}

.item-price {
    font-weight: 700;
    color: #585858;
}

.item-category,
.item-title-link,
.item-price {
    margin-bottom: 10px;
}


@media (max-width: 641px) {
    .catalog-item {
        margin-right: 0;
    }

    .item-title-link h4 {
        word-wrap: break-word;
    }
}