.burger-menu {
    display: none;
    position: relative;
    right: 0;
    top: 0;
    z-index: 2;
    width: 45px;
    height: 45px;
    margin-left: 15px;
    background-color: #fff;
    transform: translateY(-5px);
    cursor: pointer;
}

.burger-menu.active {
    background-color: var(--default-transparent);
}

.burger-menu.active .burger-menu-item.top {
    transform: translateY(8px) translateY(-50%) rotate(-45deg);
}

.burger-menu.active .burger-menu-item.middle {
    opacity: 0;
}

.burger-menu.active .burger-menu-item.bottom {
    transform: translateY(-6px) translateY(50%) rotate(45deg);
}

.burger-menu.active .burger-menu-item {
    background-color: #fff;
}

.burger-menu-inner {
    position: relative;
    width: 16px;
    height: 14px;
    margin: auto;
}

.burger-menu-item  {
    display: block;
    position: absolute;
    width: 100%;
    height: 2px;
    border-radius: 1px;
    transition: all .1s linear;
    background-color: #000;
}

.burger-menu-item.top {
    top: 0;
}

.burger-menu-item.middle {
    bottom: 0;
    margin: auto;
    top: 0;
}

.burger-menu-item.bottom {
    bottom: 0;
}


@media (max-width: 921px) {
	.burger-menu {
        display: flex;
        justify-content: center;
		align-items: center;
        z-index: 100;
	}
}


