.cart-link {
	display: flex;
	align-items: center;
}

.cart-icon-position {
	position: relative;
	transform: translateY(-5px);
}

.cart-total {
	display: flex;
	justify-content: center;
    align-items: center;
	position: absolute;
    top: -15px;
    right: -11px;
	width: 20px;
    height: 20px;
    padding: 5px;
    border-radius: 50%;
    font-size: 14px;
	text-align: center;
	background-color: #000;
    color: #fff;
}