.shop-categories {
    margin-right: 120px;
    margin-bottom: 30px;
}

.shop-categories .title {
    margin-bottom: 20px;
    color: var(--primary-green);
}

.categories-list .item {
    margin-bottom: 20px;
    padding-left: 15px;
}

.categories-list .item-link {
    margin-right: 5px;
    color: var(--primary-green);
}

@media (max-width: 1140px) {
    .shop-categories {
        margin-right: 90px;
    }
}

@media (max-width: 921px) {

    .shop-categories {
        margin-right: 0;
    }

    .categories-list {
        display: flex;
    }

    
}

@media (max-width: 300px) {
    .categories-list {
        display: block;
    }

    .shop-categories .title {
        font-size: 10vw;
    }

    .categories-list .item-link {
        word-wrap: break-word;
    }
}