.billing-form {
    display: flex;
}

.billing-form .billing-details {
    width: 60%;
    margin-right: 60px;
    padding: 35px;
    background-color: var(--default-transparent);
}

.billing-form .order-review {
    width: 40%;
    background-color: var(--default-transparent);
}

@media (max-width: 921px) {
    .billing-form {
        flex-direction: column;
        padding: 0 20px;
    }

    .billing-form .billing-details {
        min-width: 300px;
        width: 100%;
        margin-right: 0px;
    }
    
    .billing-form .order-review {
        min-width: 300px;
        width: 100%;
    }    
}