
.header.absolute {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 3;
    width: 100%;
    border: none;
}

.header .container {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    max-width: 1366px;
    height: 100px;
    padding: 0 20px;
}

.header .navigation-menu-link span {
    letter-spacing: 1.7px;
    font-weight: bold;
}



.header-group {
    display: flex;
    height: 100%;
}

@media (max-width: 921px) {
	.header-group {
        align-items: center;
    }
}