.background-section {
	padding: 100px 0 200px;
	background-image: url('https://raw.githubusercontent.com/wingedmary/stellar/main/assets/secondaryBg.jpg');
	background-size: cover;
	background-repeat: no-repeat;
	background-position: 0 0;
}

.background-section-content {
	text-align: center;
}

.background-section-content .title {
	margin-bottom: 15px;
	letter-spacing: 4.92px;
	font-weight: 400;
	font-size: 41px;
	text-transform: uppercase;
}

.background-section-content .suptitle {
	margin-bottom: 40px;
	padding: 15px 40px;
	line-height: 1.6;
    font-size: 20px;
	background-color: var(--default-transparent);
}

.background-section-content .default-button {
	justify-content: center;
}

@media (max-width: 767px) {
	
	.background-section {
		padding: 100px 0;
	}

	.background-section-content .title {
		font-size: 30px;
	}
}

@media (max-width: 300px) {
	
	.background-section-content .title {
		display: none;
	}
}

@media (max-width: 250px) {
	
	.background-section {
		display: none;
	}
}