.footer {
	padding: 30px 0;
}

.footer-group {
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.footer-group .navigation-menu-link {
	text-transform: unset;
}

.footer-group .navigation-menu-link {
	padding-right: 12px;
	font-size: 15px;
}

@media (max-width: 921px) {

	.footer {
		border-top: none;
	}
	
	.footer-group {
		flex-direction: column;
	}

	.footer-group .navigation-menu {
		display: flex;
		flex-direction: row;
		justify-content: center;
		position: unset;
		margin-bottom: 30px;
		background-color: transparent;
	}

	.footer-group .navigation-menu-item {
		padding: unset;
	}

	.footer-group .navigation-menu-link {
		border-bottom: none;
		font-size: 22px;
	}

}


@media (max-width: 542px) {

	.footer-group .navigation-menu {
		flex-direction: column;
		align-items: center;
	}

	.footer-group .navigation-menu-item {
		margin-bottom: 15px;
	}
}