.contact-form {
    display: flex;
    flex-direction: column;
    padding: 50px;
    background-color: var(--default-transparent);
}

.contact-form .form-field {
    margin-bottom: 15px;
}

.contact-form .form-field > * {
    width: 100%;
}

.contact-form .form-field textarea {
    height: 250px;
    resize: vertical;
}

@media (max-width: 542px) {

    .contact-form {
        padding: 20px;
    }
}


@media (max-width: 300px) {

    .contact-form {
        min-width: 300px;
    }
}