.cart-table {
    min-width: 600px;
    width: 100%;
    margin-bottom: 40px;
    border-collapse: collapse;
    text-align: left;
    background-color: var(--default-transparent);
}

.cart-table-head {
    background-color: var(--default-transparent);
}

.cart-table th {
    padding: 12px 17px;
    font-weight: 700;
    font-size: 17px;
}

.product-coupon {
    padding: 10px;
}

.coupon-input {
    margin-right: 20px;
}

