.navigation-menu {
    display: flex;
    height: 100%;
    margin-right: 10px;
}

@media (max-width: 921px) {
    .navigation-menu {
        display: none;
        flex-direction: column;
        position: fixed;
        top: 0;
        left: 0;
        z-index: 99;
        width: 100%;
        height: 100%;
        margin-right: 0;
        background-color: #16151b;
    }

    .navigation-menu.show {
        display: flex;
    }
}