@import url('https://fonts.googleapis.com/css2?family=Jost:ital,wght@0,100..900;1,100..900&family=Manrope:wght@200..800&display=swap');

:root {
	--default-transition: all .3s;
	--default-border: 1px solid #d9d9d9;
	--default-transparent: rgb(0 0 0 / 35%);
}

* {
	box-sizing: border-box;
}

html, body {
	height: 100%;
	margin: 0;
}

body {
	font-family: "Jost", sans-serif;
	background-image: linear-gradient(to right bottom, #26262d, #352a2f, #352a34, #35303a, #272732, #20212d, #22242d, #252a37, #15161b, #201f25, #18161d, #0d0a0f);
	background-repeat: no-repeat;
    background-attachment: fixed;
	color: #fff;
}

h1, h2, h3, h4, h5, h6 {
	color: #fff;
}

ul, h1, h2, h3, h4, h5, h6, p {
	margin: 0;
}

ul {
	padding-left: 0;
	list-style: none;
}

p {
	margin-bottom: 20px;
    line-height: 1.6;
    font-size: 17px;
}

a {
	color: #fff;
	text-decoration: none;
}

a:visited {
	color: #fff;
}

button {
	padding: 0;
	border: none;
	background: transparent;
	cursor: pointer;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
	-webkit-appearance: none;
	margin: 0;
}

input[type=number] {
	appearance: none;
	-moz-appearance: textfield;
}

img {
	display: block;
}

.container {
	max-width: 1100px;
	width: 100%;
	margin:  0 auto;
}

.styled-field {
	padding: 12px;
	border: none;
	font-size: 17px;
	background-color: var(--default-transparent);
	color: #fff;
}

.styled-field:focus-visible {
	outline: 1px solid #000;
  	border-radius: 0;
}

.styled-button {
	padding: 15px 40px;
	background-color: var(--default-transparent);
	transition: var(--default-transition);
	font-weight: 400;
	letter-spacing: 1.2px;
    text-transform: uppercase;
	white-space: nowrap;
	color: #ff9067;
	cursor: pointer;
}

.styled-button:hover {
	background-color: #000;
	color: #fff;
}

.section-title {
	margin-bottom: 20px;
	font-weight: 500;
    font-size: 41px;
}

@media (max-width: 921px) {
	.section-title {
		font-size: 25px;
	}
}

@media (max-width: 542px) {

    
}

@media (max-width: 300px) {

	p {
		word-wrap: break-word;
		word-break: break-all;
	}

	.section-title {
		display: none;
	}

	.styled-button {
		padding: 5px 15px;
        font-size: 8px;
	}
    
}