.quantity-changer {
    display: flex;
}

.quantity-field {
    width: 38px;
    height: 35px;
    background-color: transparent;
    text-align: center;
}

.quantity-field:first-child,
.quantity-field:last-child {
    color: var(--primary-green);
}

.styled-field.quantity-field {
    padding: 0;
}