.shop-sidebar {
    margin: 70px 0 40px;
    padding-top: 20px;
    padding-right: 60px;
    padding-left: 20px;
    background-color: var(--default-transparent);
}

@media (max-width: 1140px) {
    .shop-sidebar {
        padding-right: 30px;
    }
}

@media (max-width: 921px) {
    .shop-sidebar {
        margin: 0;
    }
}