.product-tabs-section {
    border-top: var(--default-border);
    background-color: var(--default-transparent);
}

.product-tabs-section .container {
    padding: 0 20px 40px 20px;
}

.product-tabs-section .tabs-list {
    display: flex;
    width: 100%;
    margin-bottom: 20px;
}

.product-tabs-section .tabs-list .tabs-item {
    margin-right: 15px;
    padding: 10px 20px;
    border-top: 2px solid transparent;
    font-weight: 700;
    cursor: pointer;
}

.product-tabs-section .tabs-list .tabs-item.active {
    border-top: 2px solid var(--primary-green);
}

.product-tabs-section .tabs-description {
    margin-bottom: 15px;
}

.product-tabs-section .tabs-description p {
    margin-bottom: 0;
}

.product-tabs-section .tabs-description ul {
    margin-left: 20px;
    list-style: disc;
}

.product-tabs-section .tabs-description ul li {
    line-height: 1.8;
}

@media (max-width: 300px) {

	.product-tabs-section .tabs-list .tabs-item {
        margin-right: 0px;
        padding: 0;
    }
    
}

