.showcase-section {
    padding: 80px 0 20px 0;
}

.showcase-section .catalog-item {
    padding: 0px;
    border: none;
}

.catalog-list {
    display: flex;
    justify-content: space-between;
    padding: 20px;
}

.catalog-list li:last-child {
    margin-right: 0;
}

@media (max-width: 921px) {
    
    .showcase-section {
        padding: 20px 0;
    }
}

@media (max-width: 641px) {
    .catalog-list {
        flex-direction: column;
    }
}