.shop-page .container {
    display: flex;
    max-width: 1366px;
    padding: 0 20px;
}

.shop-page .catalog-section .catalog-products-list li {
    width: calc(100%/3 - 20px);
}

@media (max-width: 921px) {
    .shop-page .container {
        flex-direction: column;
        padding: 0;
    }
}

@media (max-width: 641px) {
    .shop-page .catalog-section .catalog-products-list li {
        width: 100%;
    }
}