.reviews-item {
    width: calc(100% / 3);
}

.reviews-item-quotes {
    margin-bottom: 8px;
}

.reviews-item:nth-child(1) {
    padding-right: 70px;
}

.reviews-item:nth-child(2) {
    padding: 0 35px;
}

.reviews-item:nth-child(3) {
    padding-left: 70px;
}

.reviews-item-content {
    margin-bottom: 20px;
    line-height: 1.6;
    font-size: 17px;
}

.reviews-item-photo {
    margin-bottom: 20px;
    border-radius: 50%;
}

.reviews-item-name {
    letter-spacing: 2px;
    font-weight: 700;
    font-size: 12px;
    text-transform: uppercase;
}

@media (max-width: 921px) {

    .reviews-item {
        width: 100%;
        margin-bottom: 50px;
    }

    .reviews-item:last-child {
        margin-bottom: 0;
    }

    .reviews-item:nth-child(1),
    .reviews-item:nth-child(2),
    .reviews-item:nth-child(3) {
        padding: 0;
    }

    .reviews-item-photo {
        margin-right: 20px;
        float: left;
    }
}