.search-panel {
    display: flex;
    margin-bottom: 30px;
}

.styled-field.search {
    width: 100%;
    margin-right: 10px;
}

.styled-button.search-button {
    padding: 12px 17px;
}


@media (max-width: 300px) {
    .search-panel {
        flex-direction: column;
    }
    
}