.content-section {
    padding: 125px 0;
}

.content-item {
    display: flex;
}

.content-item > div {
    width: 50%;
}

.content-image {
    padding: 10px;
}

.content-image img {
    width: 100%;
}

.content-block {
    padding: 50px 50px 50px 78px;
}

.content-block .title {
    margin-bottom: 20px;
    font-size: 36px;
    text-transform: uppercase;
}

.content-block .content {
    line-height: 1.6;
    font-size: 17px;
}

@media (max-width: 767px) {
    .content-item {
        flex-direction: column;
    }

    .content-item > div {
        width: 100%;
    }

    .content-block {
        padding: 10px;
    }
}

@media (max-width: 300px) {

    .content-section {
        padding: 20px 0;
    }

    .content-image {
        display: none;
    }

    .content-block .title {
        display: none;
    }
}