.product-section {
    margin: 70px 0;
}

.product-showcase {
    display: flex;
    padding: 0 20px;
}

.showcase-image {
    margin-right: 40px;
}

.showcase-image img {
    width: 100%;
    height: 100%;
}

.product-category {
    color: var(--primary-green);
}

.product-section .product-title {
    font-size: 30px;
}

.product-section .product-price {
    font-weight: 700;
    font-size: 28px;
}

.showcase-buttons {
    display: flex;
}

@media (max-width: 921px) {
    .product-showcase {
        flex-direction: column;
    }
    
    .showcase-image {
        margin-right: 0px;
        margin-bottom: 40px;
    }
}

@media (max-width: 300px) {
    .product-section .product-title {
        font-size: 10vw;
    }
}