.main-background-section {
	position: relative;
	height: 750px;
	background-image: url('https://github.com/wingedmary/stellar/blob/main/assets/mainBg.jpg?raw=true');
	background-size: cover;
	background-repeat: no-repeat;
	background-position: center top;
}


.main-background-content {
	display: flex;
    flex-direction: column;
	position: absolute;
    bottom: 90px;
    left: 100px;
}

.main-background-content .main-title {
	width: fit-content;
	font-size: 80px;
	letter-spacing: 10px;
	text-transform: uppercase;
	text-shadow: 2px 2px black;
	background-color: var(--default-transparent);
}

.main-background-content .main-suptitle {
	margin-bottom: 25px;
    font-size: 40px;
    font-weight: 300;
    text-transform: uppercase;
    letter-spacing: 6.3px;
	background-color: var(--default-transparent);
}

@media (max-width: 1024px) {
	.main-background-content {
		bottom: 80px;
    	left: 75px;
	}

	.main-background-content .main-title {
		font-size: 60px;
		letter-spacing: 7px;
	}

	.main-background-content .main-suptitle {
		font-size: 35px;
        letter-spacing: 4px;
	}
}

@media (max-width: 921px) {

	.main-background-section {
		height: 450px;
	}

	.main-background-content {
		bottom: 30px;
    	left: 45px;
	}

	.main-background-content .main-title {
		font-size: 40px;
	}

	.main-background-content .main-suptitle {
		font-size: 20px;
		text-align: center;
	}
}

@media (max-width: 542px) {

    .main-background-content {
		align-items: center;
    	left: 50%;
		width: 100%;
		transform: translateX(-50%);
	}

	.main-background-content .main-title {
		font-size: 10vw;
	}

	.main-background-content .main-suptitle {
		font-size: 3vw;
	}
}

@media (max-width: 300px) {

	.main-background-section {
		height: 250px;
	}

	.main-background-content .main-title {
		word-break: break-word;
	}
    
}