.advantages-item {
    display: flex;
    width: calc(100% / 3);
}

.advantages-item:nth-child(1) {
    padding-right: 48px;
    border-right: 1px solid #eae9ec;
}

.advantages-item:nth-child(2) {
    padding: 0 24px;
}

.advantages-item:nth-child(3) {
    padding-left: 48px;
    border-left: 1px solid #eae9ec;
}

.advantages-icon-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    margin-right: 15px;
    border-radius: 50%;
    background-color: var(--default-transparent);
}

.advantages-item-content .title {
    margin-bottom: 12px;
    letter-spacing: 2px;
    font-size: 15px;
    text-transform: uppercase;
}

.advantages-item-content .description {
    font-size: 14px;
}

@media (max-width: 921px) {
	.advantages-item {
        flex-direction: column;
        align-items: center;
        width: 100%;
        padding-bottom: 0px;
        border-bottom: var(--default-border);
        text-align: center;
    }

    .advantages-item:nth-child(1),
    .advantages-item:nth-child(2),
    .advantages-item:nth-child(3) {
        padding: 0;
        border-right: none;
        border-left: none;
    }

    .advantages-icon-wrapper {
        margin-top: 30px;
        margin-right: 0;
        margin-bottom: 10px;
    }

    .advantages-item-content {
        margin-bottom: 30px;
    }

    .advantages-item-content .description {
        margin-bottom: 0;
    }
}

@media (max-width: 921px) {
    
    .advantages-item-content .title {
        display: none;
    }
}
