.catalog-section {
    margin: 70px 0 40px;
    padding-top: 20px;
    padding-left: 60px;
    background-color: var(--default-transparent);
}

.catalog-section .title {
    margin-bottom: 40px;
    line-height: 1.4;
    font-size: 71px;
    font-weight: 300;
    color: var(--primary-green);
}

.catalog-section .current-results {
    margin-bottom: 40px;
}

.catalog-products-list {
    display: flex;
    flex-wrap: wrap;
}


@media (max-width: 1140px) {
    .catalog-section {
        padding-left: 20px;
    }

    .catalog-section .title {
        width: fit-content;
        margin-bottom: 60px;
        padding-right: 30px;
        border-bottom: 2px solid #fff;
        font-size: 66px;
    }
}

@media (max-width: 921px) {
    .catalog-section {
        margin: 0;
    }
}

@media (max-width: 641px) {

    .catalog-section {
        padding-right: 20px;
    }

    .catalog-section .title {
        word-break: break-word;
    }

    .catalog-products-list {
        flex-direction: column;
    }
}

@media (max-width: 641px) {

    .catalog-section .title {
        font-size: 10vw;
    }
}