.contacts-section {
    margin: 120px 0;
}

.contacts-section .container {
    display: flex;
    padding: 0 20px;
}

.contacts-section .column {
    width: 50%;
}

.contacts-section .column:last-child {
    padding-left: 70px;
}

.contacts-section .column .column-title {
    margin-bottom: 20px;
    font-size: 41px;
}

.contacts-list {
    padding-top: 50px;
}

.contacts-item {
    display: flex;
    margin-bottom: 30px;
}

.contacts-item .icon-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    min-width: 42px;
    width: 42px;
    height: 42px;
    margin-right: 20px;
    border: 1px solid #fff;
    border-radius: 50%;
}

.contacts-item .title {
    margin-bottom: 10px;
    font-weight: 400;
    font-size: 16px;
    text-transform: uppercase;
}

@media (max-width: 921px) {

    .contacts-section  {
        margin: 0;
        padding: 20px 0;
    }

    .contacts-section .container {
        flex-direction: column;
        
    }

    .contacts-section .column {
        width: 100%;
        padding: 20px 0;
    }

    .contacts-section .column .column-title {
        font-size: 31px;
    }
}

@media (max-width: 542px) {
    .contacts-section .column:last-child {
        padding-left: 0px;
    }

    .contacts-section .column:last-child .column-title {
        display: none;
    }

    .contacts-list {
        padding-top: 0px;
    }

}    

@media (max-width: 300px) {
    .contacts-section .column {
        overflow-x: scroll;
    }

    .contacts-section .column:last-child {
        overflow-x: unset;
    }

    .contacts-item .icon-wrapper {
        display: none;
    }
    
}