.reviews-section {
    padding: 80px 0;
    background-color: var(--default-transparent);
}

.reviews-section .container {
    padding: 0 20px;
}

.reviews-list {
    display: flex;
    margin-top: 32px;
}

@media (max-width: 921px) {

    .reviews-section {
        padding: 20px 0;
    }

    .reviews-section .container {
        padding: 20px;
    }

    .reviews-list {
        flex-direction: column;
    }
}